
import { Component } from '@angular/core';
import { IonicPage, NavController} from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../providers';
import { Http } from '@angular/http';
import { Storage } from '@ionic/storage';
import { Geolocation} from "@ionic-native/geolocation";

import 'rxjs/add/operator/map'

@IonicPage()
@Component({
  selector: 'page-usac-rr',
  templateUrl: 'usac-rr.html',
})
export class UsacRrPage {
  information: any=[];
  items: any=[];
  public cui		     : any;
  public tipod		     : any;
  tiposus: any=[];
  xpos:number=0;
  ypos:number=0;
  constructor(  private geolocation: Geolocation, public authService:  Auth,public storage: Storage,public http: Http,public msj: NotificationProvider,public navCtrl: NavController ,public regService: Auth) {

 
    this.geolocation.getCurrentPosition()
    .then(resp => {
      this.xpos = resp.coords.latitude;
      this.ypos = resp.coords.longitude;
    
   
    })

    this.pideregistros();

   // 
  }

  ionViewDidLoad() : void
  {//this.navCtrl.push('ParticipaForms200Page',{})

  }

  pideregistros() { 


    
  this.storage.get('tiposus').then((value) => {
    this.tiposus = value.split(',');
  this.cui=this.tiposus[2]
  //this.tipod=this.tiposus[5]

//var cc=this.tiposus[5];
  
 // if(cc.length<10)

 if(this.tiposus[7]=='5d6c9aa1dba66a1c88514638')
 {

  let localData = this.http.get('assets/data/calusacRR2.json').map(res => res.json().items);
  localData.subscribe(data => {
    this.information = data;
  })
 }
 else
 {

  if(this.tiposus[6]!='Administrador')
  {
  //carnet
   
   let localData = this.http.get('assets/data/usacRR2.json').map(res => res.json().items);
   localData.subscribe(data => {
     this.information = data;
   })
  }
  else{

   
   let localData = this.http.get('assets/data/usacRR.json').map(res => res.json().items);
   localData.subscribe(data => {
     this.information = data;
   })
  }


 }    



    
        


});
  
  



  
  }
 
  toggleSection(i) {
    this.information[i].open = !this.information[i].open;
  }
 
  toggleItem(i, j) {
    this.information[i].children[j].open = !this.information[i].children[j].open;
  }


  damenu(i, item) {
   
  var  aa=''
  aa=item.modulo
  var vv=aa.split('Â°')

   

  switch (vv[0]) {
    case 'link':
       window.open(vv[1] ,'_blank', '');
    break;
    case 'MensajeviewPage':
         this.navCtrl.push(vv[0],{ html : item.html,information:item.information,link:vv[1] });
    break;

    case 'otro':
    this.navCtrl.push(vv[1], item);
    break;
    default :
    
      
  }

 
    

    //this.navProxy.pushDetail(, item);
  }

  addanomalia() : void
  {
    this.navCtrl.push( 'ParticipaForms200Page',{})
   
  }

}



